div.mainContainerSection4 {
    height: 50vh;
    width: 100%;
    border-top: 5px solid black;
    background-position: left;
    background-repeat: no-repeat;
    transition: 0.5s;
}
div.mainContainerSection4 div.shadowMainContainer {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    background: rgb(230,230,232);
    background: linear-gradient(270deg, rgba(230,230,232,0) 77%, rgba(0,0,0,1) 100%);
}
div.mainContainerSection4 div.shadowMainContainer h2 {
    margin-left: 1vw;
    padding: 0;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
    font-size: 2.5vw;
    cursor: pointer;
}

div.mainContainerSection4 div.shadowMainContainer div.leftContainer {
    width: 100%;
    height: 50vh;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.leftBox {
    width: 100%;
    height: 100%;
    background-position: -10%;
    background-repeat: no-repeat;
    overflow: hidden;
    transition: 0.5s;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.leftBox div.shadowLeftBox {
    width: 100%;
    height: 100%;
    background: rgb(230,230,232);
    background: linear-gradient(270deg, rgba(230,230,232,0) 43%, rgba(0,0,0,1) 100%);
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
div.mainContainerSection4 div.shadowMainContainer div.leftContainer div.triangleLeft {
    width: 0;
    height: 0;
    border-bottom: 50vh solid transparent;
    border-right: 4vw solid rgba(0,0,0,1);
}

