div.contactMainContainer {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    background-size: 20% 50%;
    background-position: 10%;
    background-repeat: no-repeat;
}
div.contactMainContainer div.triangle {
    width: 0;
    height: 0;
    border-top: 82vh solid transparent;
    border-right: 30vw solid rgba(4, 104, 191, 0.80);
}
div.contactMainContainer div.rightBox {
    width: 45vw;
    height: 82vh;
    background-image: linear-gradient(to right, rgba(4,104,191,0.80), rgba(4, 104, 191,1));
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 3vw;
}
div.contactMainContainer div.rightBox h1 {
    font-size: 50px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.contactMainContainer div.rightBox h2 {
    font-size: 40px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.contactMainContainer div.rightBox h3 {
    font-size: 30px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
}
div.contactMainContainer div.rightBox p {
    font-size: 20px;
    color: whitesmoke;
    font-family: 'Roboto', sans-serif;
    font-weight: bold;
}
